        'https://stakwork-uploads.s3.amazonaws.com/knowledge-graph-joe/content-images/ea2cf2ae-76de-4185-818c-384b3b06228b.jpg',
      keyword: true,
      link: 'https://www.youtube.com/watch?v=WxGMeufy8jw',
